.footer__wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  gap: 25px;
  padding: 5px 0 50px;

  @include vp-1199 {
    gap: 23px;
  }

  @include vp-767 {
    flex-direction: column;
    justify-content: center;
    gap: 12px;
    padding: 5px 0 40px;
  }
}
